import React, { useCallback, useEffect, useState } from 'react'
import { MapContext, SourceProps, LayerProps } from 'react-map-gl'
import { useRecoilState, useRecoilValue } from 'recoil'
import {
    baseLayerAtom,
    MapLayerState,
    mapLoadingAtom,
    RenderableMapLayer,
    renderableMapLayersSelector
} from '../recoil'
import { legendColors } from '../config'
import defaultSymbology from '../components/map/defaultSymbology'
import log from 'loglevel'
import { ProjectTreeLayerTypes, TileService } from '@riverscapes/common'

function getLayerKey(lid: number): string {
    return `webRAVE_${lid || 'UNKNOWN'}`
}

function getSourceKey(layerPath: string): string {
    return `webRAVE_${layerPath}`
}

const MapLayers: React.FC = () => {
    const [retry, setRetry] = useState<number>(0)
    const { map } = React.useContext(MapContext)
    const baseLayer = useRecoilValue(baseLayerAtom)
    const [mapLoading, setMapLoading] = useRecoilState(mapLoadingAtom)
    const renderableMapLayers = useRecoilValue(renderableMapLayersSelector)

    const recalcMapLayers = useCallback(() => {
        if (!map || !map.isStyleLoaded()) {
            setMapLoading(true)
            throw new Error('Map is still loading')
        }
        setMapLoading(false)

        const getMapStyle = (): { layers: string[]; sources: string[] } => {
            const defaultVal = { layers: [], sources: [] }
            try {
                const mapStyles = map.getStyle()
                const mapLayers = mapStyles.layers
                const mapSources = mapStyles.sources
                // Get all currently loaded layers and sources from the mapStyle object
                const currLayerIds = mapLayers.map(({ id }) => id).filter((id) => id.indexOf('webRAVE') === 0)
                const currSourceIds = Object.keys(mapSources).filter((id) => id.indexOf('webRAVE') === 0)
                return { layers: currLayerIds, sources: currSourceIds }
            } catch (e) {
                // log.error('MapLayers Error retrieving styles', e)
                return defaultVal
            }
        }

        // Make a quick lookup [layerMapboxId] => {layer, sourceMapboxId}
        const renderablelayersDict = renderableMapLayers.reduce<
            Record<string, { layer: RenderableMapLayer; sourceId: string }>
        >(
            (acc, lyr) => ({
                ...acc,
                [getLayerKey(lyr.leaf.id)]: { layer: lyr, sourceId: getSourceKey(lyr.leaf.layerPath || 'UNKNOWN') }
            }),
            {}
        )

        let lastId
        // Add any new layers. We reverse them because we want the beforeId property to be correct
        Object.keys(renderablelayersDict).forEach((newKey) => {
            const { layer, sourceId } = renderablelayersDict[newKey]
            // Keep track of the layers we're adding here for each tree leaf
            const addedLayers: string[] = []
            const mapStyle = getMapStyle()
            // Layer has already been added. We can set the layer order but otherwise do nothing
            if (mapStyle.layers.indexOf(newKey) < 0) {
                // Add the source (if necessary)
                if (mapStyle.sources.indexOf(sourceId) < 0) {
                    if (layer.leaf.layerType === ProjectTreeLayerTypes.RASTER) {
                        map.addSource(sourceId, getRasterSource(layer.tiles as TileService, layer.leaf.symbology))
                    } else {
                        map.addSource(sourceId, getVectorSource(layer.tiles as TileService))
                    }
                }
                // Add the layer (every time)
                if (layer.leaf.layerType === ProjectTreeLayerTypes.RASTER) {
                    map.addLayer(getRasterLayerProps(layer, newKey, sourceId))
                    addedLayers.push(newKey)
                } else {
                    let symbJSON = (layer.symbology?.symbologyJSON || []) as Record<string, unknown>[]
                    // If we need a default fallback symbology then go create one
                    if (symbJSON && layer.randomColor !== null) {
                        const rndCol = legendColors[layer.randomColor]
                        switch (layer.leaf.layerType) {
                            case ProjectTreeLayerTypes.LINE:
                                symbJSON = [defaultSymbology.line(layer.leaf, rndCol, layer.tiles)]
                                break
                            case ProjectTreeLayerTypes.POLYGON:
                                symbJSON = [
                                    defaultSymbology.polygon(layer.leaf, rndCol, layer?.tiles),
                                    defaultSymbology.outline(layer.leaf, rndCol, layer?.tiles)
                                ]
                                break
                            case ProjectTreeLayerTypes.POINT:
                                symbJSON = [defaultSymbology.line(layer.leaf, rndCol, layer.tiles)]
                                break
                            default:
                                break
                        }
                    }
                    symbJSON.forEach((symb, idx) => {
                        const idStrIdx = idx === 0 ? newKey : `${newKey}-${idx}`
                        const lyrProps = getVectorLayerProps(layer, symb, idStrIdx, sourceId)
                        map.addLayer(lyrProps)
                        addedLayers.push(idStrIdx)
                    })
                }
                log.debug('MapLayers::AddLayer', { layer, sourceId, newKey, lastId })
            } else {
                // If this layer is already loaded then we probably just have to consider
                // What order it's in
                const applicableLayers = getMapStyle().layers.filter((lid) => lid.indexOf(newKey) > -1)
                applicableLayers.forEach((lKey) => addedLayers.push(lKey))
            }
            // Regardless of the layer being found we might need to reorder it
            addedLayers.forEach((addKey) => {
                if (lastId) {
                    // const currLayers = getMapStyle().layers
                    log.debug(`MapLayers:: Moving ${addKey} before ${lastId}`)
                    // const currIndex = currLayers.indexOf(addKey)
                    // const lastIndex = currLayers.indexOf(lastId)
                    // Moving layers is expensive so don't do it unless you have to
                    // if (currIndex > -1 && lastIndex > -1 && currIndex !== lastIndex + 1) {
                    map.moveLayer(addKey, lastId)
                    // }
                }
                lastId = addKey
            })
        })

        // Remove any unused Layers
        // TODO: Right now we don't clean up sources. Just layers
        getMapStyle().layers.forEach((currId) => {
            // Remember a webRave layer can represent more than one mapbox layer
            if (!Object.keys(renderablelayersDict).find((newKey) => currId.indexOf(newKey) == 0)) {
                map.removeLayer(currId)
            }
        })
        // Reset any retries we might be looking into
        setRetry(0)
    }, [map, mapLoading, renderableMapLayers, baseLayer])

    // When baselayer changes we reset the map
    useEffect(() => {
        try {
            log.info('recalcMapLayers:Regular')
            recalcMapLayers()
        } catch (e) {
            if (retry < 20) setTimeout(() => setRetry(retry + 1), 2000)
        }
    }, [map, mapLoading, renderableMapLayers, baseLayer])

    useEffect(() => {
        try {
            log.info('recalcMapLayers:Regular')
            if (retry > 0) recalcMapLayers()
        } catch (e) {
            if (retry < 20) setTimeout(() => setRetry(retry + 1), 2000)
        }
    }, [retry])

    // This is kind of a bogus react component
    return null
}

export default MapLayers

function getVectorSource(tiles: TileService): SourceProps {
    return {
        type: 'vector',
        maxzoom: tiles.maxZoom || 20,
        minzoom: tiles.minZoom || 4,
        tiles: [`${tiles.url}{z}/{x}/{y}.${tiles.format || 'pbf'}`]
    }
}
function getRasterSource(tiles: TileService, symbologyName?: string): SourceProps {
    return {
        type: 'raster',
        scheme: 'tms',
        maxzoom: tiles.maxZoom || 20,
        minzoom: tiles.minZoom || 4,
        tiles: [`${tiles.url}${symbologyName || 'raster'}/{z}/{x}/{y}.${tiles.format || 'png'}`]
    }
}

function getRasterLayerProps(layer: MapLayerState, id: string, sourceId: string): LayerProps {
    const lyrProps: LayerProps = {
        type: 'raster',
        id,
        source: sourceId,
        minzoom: layer.tiles?.minZoom || 0,
        maxzoom: 24,
        layout: {},
        paint: {}
    }
    return lyrProps
}

function getVectorLayerProps(layer: MapLayerState, symb: Record<string, unknown>, id: string, sourceId: string) {
    const lyrProps = {
        // this will get overridden by
        type: 'fill',
        minzoom: layer.tiles?.minZoom || 0,
        maxzoom: 24, // hardcoded to the max value possible
        layout: {},
        paint: {},
        ...symb,
        id,
        source: sourceId
    }
    return lyrProps
}
