import React from 'react'
import IconButton from '@material-ui/core/IconButton'
import ListItem from '@material-ui/core/ListItem'
import ListItemAvatar from '@material-ui/core/ListItemAvatar'
import Avatar from '@material-ui/core/Avatar'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import ListItemText from '@material-ui/core/ListItemText'
import CircularProgress from '@material-ui/core/CircularProgress'

import CancelIcon from '@material-ui/icons/Cancel'
import { LayerIcon } from '@riverscapes/react-icons'
import { ProjectTreeLayerTypes } from '@riverscapes/common'

// import { legendColorFinder } from '../../lib/util'

import { makeStyles } from '@material-ui/core/styles'
// import { makeStyles, useTheme } from '@material-ui/core/styles'

const useStyles = makeStyles((theme) => ({
    container: {},
    listItem: {
        paddingLeft: 0
    },
    listItemAvatar: {
        minWidth: theme.spacing(6)
    },
    avatar: {},
    root: {
        marginTop: theme.spacing(2)
    },
    heading: {
        marginTop: theme.spacing(1)
    },
    loadingSpinner: {
        color: theme.palette.text.secondary
    },
    layerItem: {
        display: 'flex'
    },
    legendName: {
        flexGrow: 1
    },
    deleteButtonHover: {},
    legendColor: {
        border: '1px solid black',
        width: '20px',
        display: 'inline-block',
        height: '14px',
        marginRight: '5px'
    },
    deleteButton: {
        flexGrow: 0,
        color: '#AAAAAA'
    }
}))

export interface LegendLayerItemProps {
    label: string
    leafId: number
    loading?: boolean
    // TODO: This is largely innadequate long term and doesn't handle rasters or color ramps
    fgColor?: string
    bgColor?: string
    subLabel?: string
    layerType: ProjectTreeLayerTypes
    removeLayer: (lid: number) => void
}

const LegendLayerItem: React.FC<LegendLayerItemProps> = ({
    label,
    leafId,
    loading,
    fgColor,
    bgColor,
    subLabel,
    layerType,
    removeLayer
}: LegendLayerItemProps) => {
    const classes = useStyles()
    const bgColFinal = layerType === ProjectTreeLayerTypes.RASTER ? '#FFFFFF' : bgColor || '#66666666'
    const fgColFinal = layerType === ProjectTreeLayerTypes.RASTER ? '#000000' : fgColor || '#000000'

    return (
        <ListItem dense className={classes.listItem}>
            <ListItemAvatar className={classes.listItemAvatar}>
                <Avatar style={{ background: bgColFinal }}>
                    <LayerIcon
                        layerType={layerType}
                        iconProps={{
                            style: { color: fgColFinal }
                        }}
                    />
                </Avatar>
            </ListItemAvatar>
            <ListItemText primary={label} secondary={subLabel} />
            <ListItemSecondaryAction>
                {loading && (
                    <CircularProgress className={classes.loadingSpinner} color="inherit" thickness={3} size={15} />
                )}
                {!loading && (
                    <IconButton
                        edge="end"
                        aria-label="delete"
                        onClick={(event) => {
                            event.stopPropagation()
                            removeLayer(leafId)
                        }}
                    >
                        <CancelIcon />
                    </IconButton>
                )}
            </ListItemSecondaryAction>
        </ListItem>
    )
}

export default LegendLayerItem
