import { atom, atomFamily, selectorFamily } from 'recoil'
import { endpoints } from '../config'
// import { orange } from '@material-ui/core/colors'
import { TileService, TilingStatus, getLayerTiles } from '@riverscapes/common'
import downloadQueue from '../lib/downloadQueue'
// import log from 'loglevel'

import { authAtom, projectAtom } from './project'

/****************************************
 * Tile Service Library
 ****************************************/
// This is using the requestId pattern found here: https://recoiljs.org/docs/guides/asynchronous-data-queries/
// This atom does nothign but track the requests made
export const tilesRequestIdAtomFamily = atomFamily<number, string>({
    key: 'tiles/tileReq',
    default: 0
})
// Request and retry are subtly different. Requests can be used as a generic way to refresh data (valid or not)
// Retries get incremented only when there's an error
export const tilesRetriesAtomFamily = atomFamily<number, string>({
    key: 'tiles/tileRetries',
    default: 0
})

export const usableIdsAtom = atom<number[]>({
    key: 'tiles/usableIds',
    default: []
})

export const tilesSelectorFamily = selectorFamily<TileService, string>({
    key: 'tiles/tiles',
    get:
        (layerPath) =>
        async ({ get }) => {
            // By just including this here (even if we don't assign it) we add tilesRequestIdAtomFamily
            // as a rippling dependency so updating tilesRequestIdAtomFamily will cause this query to refresh
            get(tilesRequestIdAtomFamily(layerPath))
            // Retries are for errors
            get(tilesRetriesAtomFamily(layerPath))

            const auth = get(authAtom)
            const proj = get(projectAtom)

            // TODO: Set Priority here
            // ATOM LOOKUP PRIORITY
            let retVal: TileService = {
                status: TilingStatus.UNKNOWN
            }
            if (!proj || !auth || !layerPath) return retVal

            // Make the Asynchronous call to the API here
            // A priority between 0 and 9. A job with a priority of 4 will be queued ahead of a job with a priority of 5
            // Default is 5
            try {
                const apiRes = await downloadQueue.schedule({ priority: 5 }, () =>
                    auth
                        .getCurrentToken()
                        .then((jwt) =>
                            getLayerTiles(endpoints.graphql, jwt, proj.projType, proj?.program, proj?.id, layerPath)
                        )
                )
                if (!apiRes || apiRes.errors || !apiRes.data) {
                    // Tiling errors don't get retried. We reached the server correctly so
                    // Any further request will likely be met with a similar result
                    retVal.status = TilingStatus.FETCH_ERROR
                    if (apiRes.errors && apiRes.errors.length > 0) {
                        retVal.error = apiRes.errors.map((e) => JSON.stringify(e)).join(', \n')
                    }
                }
                retVal = {
                    ...retVal,
                    ...apiRes.data?.getLayerTiles
                }
            } catch (err) {
                retVal.status = TilingStatus.FETCH_ERROR
                retVal.error = err.toString()
            }
            return retVal
        }
})
