import React from 'react'
import Legend from '../components/legend/Legend'
import { useRecoilState, useRecoilValue } from 'recoil'
import useMapLayers from '../hooks/useMapLayers'
import {
    baseLayerAtom,
    activeViewAtom,
    projectAtom,
    layerStateSelectorFamily,
    activeLeavesSortedSelector,
    leafRandomColAtomFamily,
    treeLeafSelectorFamily,
    mapErrorAtom
} from '../recoil'
import { legendColors } from '../config'
import baseLayers from '../mapStyles'
import { legendColorFinder } from '../lib/util'
import { ProjectTreeView, SymbologyStatus, TilingStatus } from '@riverscapes/common'
import LegendLayerItem from '../components/legend/LegendLayerItem'
import useViews from '../hooks/useViews'
import log from 'loglevel'

const LegendConnect: React.FC = () => {
    const proj = useRecoilValue(projectAtom)
    const mapError = useRecoilValue(mapErrorAtom)
    const activeLeaves = useRecoilValue(activeLeavesSortedSelector)
    const [baseLayer, setBaseLayer] = useRecoilState(baseLayerAtom)
    const currentView = useRecoilValue(activeViewAtom)
    const { setView } = useViews()

    if (!proj) return null

    const views: ProjectTreeView[] = proj.tree.views ? proj.tree.views : []
    const defaultView = views.length > 0 && proj.tree.defaultView ? proj.tree.defaultView : ''
    return (
        <Legend
            views={views}
            defaultView={defaultView}
            baseLayer={baseLayer}
            baseLayers={baseLayers}
            error={mapError}
            setBaseLayer={setBaseLayer}
            viewId={currentView}
            setViewId={setView}
        >
            {activeLeaves.map(({ id }, idx) => (
                <LegendItemConnect key={`lgdLyr-${idx}`} lid={id} />
            ))}
        </Legend>
    )
}

export default LegendConnect

interface ItemProps {
    lid: number
}

const LegendItemConnect: React.FC<ItemProps> = ({ lid }: ItemProps) => {
    const leaf = useRecoilValue(treeLeafSelectorFamily(lid))
    const layer = useRecoilValue(layerStateSelectorFamily(lid))
    const rndColIdx = useRecoilValue(leafRandomColAtomFamily(lid))
    const { removeLayer } = useMapLayers()

    const cols = legendColorFinder(
        layer.symbology?.symbologyJSON,
        rndColIdx !== null ? legendColors[rndColIdx] : undefined
    )
    const stats = layer.tiles?.rasterStats?.stats as Record<string, unknown>
    let subLabel = ''
    if (stats && stats.maximum && stats.minimum) {
        try {
            subLabel = `min: ${(stats.minimum as number).toFixed(2)} max: ${(stats.maximum as number).toFixed(2)}`
        } catch {
            log.error(`Could not get stats using object: ${JSON.stringify(stats)}`)
        }
    }

    const { id, layerType, label } = leaf
    return (
        <LegendLayerItem
            leafId={id}
            fgColor={cols.fg}
            bgColor={cols.bg}
            layerType={layerType}
            label={label}
            subLabel={subLabel}
            loading={layer.tileStatus === TilingStatus.FETCHING || layer.symbologyStatus === SymbologyStatus.FETCHING}
            removeLayer={removeLayer}
        />
    )
}
