import React from 'react'
import LegendConnect from './Legend.connect'
// import MapLayerConnect from './MapLayer.connect'
import { useRecoilValue, useRecoilState } from 'recoil'
import { baseLayerAtom, boundsSelector, drawerOpenAtom, mapLoadingAtom } from '../recoil'
import Map from '../components/map/Map'
import TreeToggleBtn from '../components/TreeToggleBtn'
import MapLayerConnect from './MapLayer.connect'

const MapConnect: React.FC = () => {
    const bounds = useRecoilValue(boundsSelector)
    const mapLoading = useRecoilValue(mapLoadingAtom)

    const baseLayer = useRecoilValue(baseLayerAtom)
    const [drawerOpen, toggleDrawer] = useRecoilState(drawerOpenAtom)

    return (
        <React.Fragment>
            <Map baseLayer={baseLayer} zoomBounds={bounds.zoom} bounds={bounds.bounds} styleLoading={mapLoading}>
                {[
                    <TreeToggleBtn
                        key="treeToggleBtn"
                        drawerOpen={drawerOpen}
                        toggleDrawer={() => toggleDrawer(!drawerOpen)}
                    />,
                    <LegendConnect key="legend" />,
                    <MapLayerConnect key="mapLayers" />
                ]}
            </Map>
        </React.Fragment>
    )
}

export default MapConnect
