import React from 'react'
import { History } from 'react-router-dom'
import AppWrapper from '../components/AppWrapper'
// import log from 'loglevel'
// import { useHosted } from '@northarrowresearch/react-auth'
import { UseHostedReturn } from '@northarrowresearch/react-auth'
import ProjectLoading from '../components/ProjectLoading'
import { useDownloads, NotAllowedModal, useProgram, useProject } from '@riverscapes/react-common'
import { Program, WarehouseRoleEnum } from '@riverscapes/common'

import useAppStateInit from '../hooks/useAppStateInit'
import { drawerOpenAtom, projectLayerPathsSelector } from '../recoil'
import { useRecoilValue } from 'recoil'
import ProjectNotFound from '../components/ProjectNotFound'
import AppSplitter from '../components/AppSplitter'
import TreeDrawer from '../components/TreeDrawer'
import TreeControlConnect from './Treecontrol.connect'
import MapTilesConnect from './MapTiles.connect'
import MapConnect from './Map.connect'
import ProjectInfoTab from '../components/ProjectInfoTab'
import useColourFallback from '../hooks/useColourFallback'

/**
 * This is the over-arching wrapper for the app. It combines all the
 * same place. Time will tell if that's a good strategy.
 * This is also where we carry the bulk of the state and the hooks for the app
 */
interface Props {
    auth: UseHostedReturn
    history: History
    programs: Program[]
}

const AppConnect: React.FC<Props> = ({ history, auth, programs }: Props) => {
    // Here we make the graphql call to retrieve the project and program from the API
    const program = useProgram(programs)
    const drawerOpen = useRecoilValue(drawerOpenAtom)
    const projectLayerPaths = useRecoilValue(projectLayerPathsSelector)

    // Init state won't change unless I reload this component
    const hasViewAccess = program && Object.values(WarehouseRoleEnum).indexOf(program.role) > -1
    const { project, loading: isProjLoading, initialized } = useProject(auth, program, false, true)
    const { downloadZipFile, pendingZips } = useDownloads(auth, program as Program)

    // This is where we initialize all the recoil pre-fetch stuff
    useAppStateInit(auth, project)
    useColourFallback()

    // 1. If we don's have access then tell the user that
    if (!hasViewAccess)
        return (
            <AppWrapper title={'Access Denied'} history={history} signOut={auth.signOut}>
                <NotAllowedModal program={program as Program} onClose={(): void => history.push('/')} />
            </AppWrapper>
        )
    // 2. If we haven't made the query yet then this is in LOADING state
    else if (!initialized || isProjLoading)
        return (
            <AppWrapper title={'Loading'} history={history} signOut={auth.signOut}>
                <ProjectLoading title={isProjLoading ? 'Loading Project...' : 'Initializing...'} />
            </AppWrapper>
        )
    // 4. Default to project not found otherwise
    else if (!project)
        return (
            <AppWrapper title={'Project not found'} history={history} signOut={auth.signOut}>
                <ProjectNotFound />
            </AppWrapper>
        )

    return (
        <>
            {projectLayerPaths.map((plp) => (
                // This is maybe a little bit of a hack and a small concession. Usually I don't like dummy
                // React components that return nothing but in this case I can't figure out a way to do this nicely
                <MapTilesConnect key={plp} layerPath={plp} />
            ))}
            <AppWrapper title={project.name} history={history} signOut={auth.signOut}>
                <AppSplitter
                    open={drawerOpen}
                    split="vertical"
                    primary="second"
                    minSize={500}
                    maxSize="85%"
                    defaultSize="75%"
                    child1={
                        <TreeDrawer
                            tabs={[
                                {
                                    label: 'Project Tree',
                                    value: <TreeControlConnect />
                                },
                                {
                                    label: 'Project Info',
                                    value: (
                                        <ProjectInfoTab
                                            project={project}
                                            downloadZipFile={downloadZipFile}
                                            pendingZips={pendingZips}
                                        />
                                    )
                                }
                            ]}
                        />
                    }
                    child2={<MapConnect />}
                />
            </AppWrapper>
        </>
    )
}
export default AppConnect
