import React, { useState, useRef, useEffect } from 'react'
import { config } from '@riverscapes/react-common'
import MapGL, { ViewportProps } from 'react-map-gl'
import { makeStyles } from '@material-ui/core/styles'
import 'mapbox-gl/dist/mapbox-gl.css'
import MapLoading from './MapLoading'
import mapStyles from '../../mapStyles'
import { BaseLayer } from '../../types'
import { CircularProgress } from '@material-ui/core'
import _ from 'lodash'
// import log from 'loglevel'

interface MapProps {
    baseLayer: BaseLayer
    styleLoading?: boolean
    bounds?: [minx: number, miny: number, maxx: number, maxy: number]
    zoomBounds: [minZoom: number, maxZoom: number]
    children?: React.ReactChild | React.ReactChildren | React.ReactElement[]
}

const useStyles = makeStyles((theme) => ({
    root: {
        marginLeft: theme.spacing(1)
    },
    container: {
        display: 'flex',
        height: '100%'
    },
    mapContainer: {
        position: 'relative',
        background: '#CCCCCC',
        height: '100%',
        width: '100%',
        '&>h1': {
            color: '#9999FF',
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)'
        }
    },
    drawerContents: {
        // border: '1px solid green',
        height: '100%',
        display: 'flex'
    },
    drawerSpacer: {
        flex: '0 0 0%'
    },
    drawerSpacerOpen: {
        // flex: `0 0 ${DRAWER_WIDTH}`
    },
    dialogContainer: {
        alignItems: 'left',
        justifyContent: 'left'
    },
    loaderSpinner: {
        position: 'absolute',
        bottom: 10,
        right: 10,
        color: theme.palette.secondary.main
    },
    dialog: {
        position: 'absolute',
        margin: 0,
        left: '20px',
        top: '20px'
    },
    subheaders: {
        background: 'white'
    },
    dummyText: {}
}))

const defaultViewport: ViewportProps = {
    // longitude: -115.8,
    // latitude: 46.1,
    zoom: 8
    // bearing: 0,
    // pitch: 0
}

const debounceConsoleLog = _.debounce((...vals) => console.log(...vals), 1500)

const Map: React.FC<MapProps> = ({ children, baseLayer, styleLoading, bounds, zoomBounds }: MapProps) => {
    const classes = useStyles()
    const [viewport, _setViewport] = useState<ViewportProps>(defaultViewport)
    const viewportRef = useRef<ViewportProps>()
    viewportRef.current = viewport

    // Constrain the Bounds and zoom
    const setViewport = (inV: ViewportProps) => {
        const outV = { ...viewportRef.current, ...inV, pitch: 0, bearing: 0 }
        // Constrict bounds
        if (zoomBounds && outV.zoom) {
            if (outV.zoom > zoomBounds[1]) outV.zoom = zoomBounds[1]
            else if (outV.zoom < zoomBounds[0]) outV.zoom = zoomBounds[0]
        }
        if (bounds && outV.longitude && outV.latitude) {
            if (outV.longitude < bounds[0]) outV.longitude = bounds[0]
            else if (outV.longitude > bounds[2]) outV.longitude = bounds[2]
            if (outV.latitude < bounds[1]) outV.latitude = bounds[1]
            else if (outV.latitude > bounds[3]) outV.latitude = bounds[3]
        }

        debounceConsoleLog('setViewport DEBOUNCED', { inV, outV, bounds })
        _setViewport(outV)
    }

    const baseStyle = mapStyles.find((bs) => bs.id === baseLayer.id) as BaseLayer

    // When loading we may get an initial zoom and/or bounds value
    useEffect(() => {
        const newViewport = { ...viewportRef.current }
        // First set
        if (!newViewport.latitude && bounds) {
            setViewport({
                ...newViewport,
                longitude: (bounds[0] + bounds[2]) / 2,
                latitude: (bounds[1] + bounds[3]) / 2
            })
        }
    }, [bounds, zoomBounds])

    const mapLoader = styleLoading && <CircularProgress size="2rem" thickness={6} className={classes.loaderSpinner} />

    // NO HOOKS BELOW HERE
    if (!baseStyle || !bounds) return <MapLoading />
    return (
        <div className={classes.mapContainer}>
            <MapGL
                {...viewport}
                width="100%"
                height="100%"
                minZoom={zoomBounds[0] || 4}
                maxZoom={zoomBounds[1] || 20}
                mapStyle={baseStyle.style}
                clickRadius={2}
                onViewportChange={setViewport}
                mapboxApiAccessToken={config.mapConfig.mapBoxToken}
            >
                {mapLoader}
                {children}
            </MapGL>
        </div>
    )
}

export default Map
